/*

====================== MAIN ======================  */

.zetta-menu {
  padding: 0 !important;
  display: inline-block;
  position: relative;
  margin:0 !important;
  text-align:center;

}
.zetta-menu:before,
.zetta-menu ul,
.zetta-menu li,
.zetta-menu div,
.zetta-menu a,
.zetta-menu label,
.zetta-menu input,
.zetta-menu textarea {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  outline: none;
  border: 0;
  list-style: none;
}
.zetta-menu a {
  cursor: pointer;
  text-decoration:none;
}
.zetta-menu img{float:left; padding: 8px 0 0 0;}
.zetta-menu h1, .zetta-menu h5, .zetta-menu h6 {font-weight: normal;}
.zetta-menu h2, .zetta-menu h3, .zetta-menu h4{text-align: center;
  margin: 0;
  padding: 15px 0;
  color: #3598dc;
}
.zetta-menu.zm-full-width {
  display: block;
  float:right;
}

.zetta-menu.zm-fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.zetta-menu.zm-fixed.zm-position-left {
  top: 0;
  bottom: 0;
  left: 0;
  right: auto;
}
.zetta-menu.zm-fixed.zm-position-right {
  top: 0;
  bottom: 0;
  left: auto;
  right: 0;
}
.zetta-menu.zm-fixed.zm-position-bottom {
  top: auto;
  bottom: 0;
  left: 0;
  right: 0;
}
.zetta-menu,
.zetta-menu a,
.zetta-menu a:visited {
  font-size:14px;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: bold !important;
  line-height: 35px !important;
}

.zetta-menu a:hover {
	
	 color: #f69527;

}

.zetta-menu a:hover {
	
	 color: #f69527;

}

.zetta-menu .ativo {
	
	color:#f69527 !important;
	border:1px solid #f69527;

}

.zetta-menu > li:hover > a {
	font-weight: normal;
	line-height: 51px;
	background:none;
}

.zetta-menu > li > a {
  font-weight: normal;
  padding: 0 13px;
  line-height: 51px;
}

.zetta-menu > li:hover .sm{
	color:#FFFFFF;
	font-weight: normal;
	display:block;
	padding:9px 0 !important;
}

.zetta-menu > li:hover .sm:hover{
	color:#dd282c !important;
	font-weight: normal;
	padding:9px 0 !important;
}



.zetta-menu > li {
  display: block;
  vertical-align: middle;
  text-align:center;
  float: left;
  height: 35px;

}
.zetta-menu > li.zm-right-item {
  margin-right: 0;
  float: right;
}

.zetta-menu > li:hover,
.zetta-menu > li.zm-active {

}
.zetta-menu li {
	z-index:999;
  position: relative;
  cursor: default;

}
.zetta-menu li > a {
  text-decoration: none;
  display: block;
}
.zetta-menu li > ul,
.zetta-menu li > div {
  z-index: 1;
}
.zetta-menu.zm-position-left > li,
.zetta-menu.zm-position-right > li {
  position: relative !important;
}
.zetta-menu.zm-position-left > li,
.zetta-menu.zm-position-right > li,
.zetta-menu.zm-position-left > li.zm-logo,
.zetta-menu.zm-position-right > li.zm-logo {
  float: none;
}
.zetta-menu.zm-position-left > li > a .zm-caret,
.zetta-menu.zm-position-right > li > a .zm-caret {
  float: right;
}
.zetta-menu .fa {
  font-size: 12pt;
  letter-spacing: 0px;
  line-height: inherit;
}
.zetta-menu .zm-caret {
  letter-spacing: 0;
  float: right;
  margin-left: 15px;
}
.zetta-menu > li > a .zm-caret {
  float: none;
}
.zetta-menu .zm-single-icon {
  letter-spacing: 0;
}
.zetta-menu:after {
  content: '';
  display: block;
  clear: both;
}
/* 

====================== LOGO ======================

*/
.zetta-menu > li.zm-logo {
  float: left;
  margin-left: 0;
